.title {
  margin: 0.6rem;
}

.forecast {
  display: flex;
}

.left {
  flex: auto;
  padding: 0 2rem;
}

.left img,
.left-temp {
  display: inline-block;
  vertical-align: top;
}

.left-temp {
  margin: auto 1rem;
}

.left-temp-main {
  font-weight: bold;
  font-size: x-large;
  margin-bottom: 0.2em;
}

.left-temp-secondary {
  margin: 0;
}

.right {
  display: flex;
  flex-direction: column;
  padding: 0 3rem;
  justify-content: center;
}

.right p {
  margin: 5px 0;
}

@media (max-width: 768px) {
  .forecast {
    flex-direction: column;
    align-items: center;
  }
}